import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'summary'
})
export class SummaryPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    if(!value)
     return null;

    return value.substr(0, 100)+ '...';
    }

}



